.Grid-btn-wrapper {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: end;
	    justify-content: flex-end;
	-ms-flex-align: center;
	    align-items: center;
	padding:1%;
	margin:2%;

}

.Grid-btn, .Disable-Grid-btn {
	height: 35px;
    width: 20%;
    border: 1px solid #596265;
    border-radius: 3px;
    background-color: #187E97;
    color: #ffffff;
}

.Disable-Grid-btn:hover {
	-webkit-transition:none;
	-o-transition:none;
	transition:none;
	cursor:not-allowed;
}

.Grid-btn:hover {
	-webkit-transition: all 0.3s ease;
	-o-transition: all 0.3s ease;
	transition: all 0.3s ease;
  	-webkit-transform: scale(0.9);
  	-ms-transform: scale(0.9);
  	transform: scale(0.9);
  	cursor: pointer;
}




